:root {
  --background: #f6f8ff;
  --foreground: #0f172a;
}
@layer base {
  body {
    font-family: var(--font-geist-sans), system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
    background: radial-gradient(1200px 800px at 10% -10%, rgba(143,135,241,0.35), transparent),
      radial-gradient(1000px 700px at 90% 0%, rgba(198,142,253,0.25), transparent),
      radial-gradient(900px 600px at 50% 100%, rgba(254,210,226,0.3), transparent),
      linear-gradient(180deg, #fdf7ff 0%, #f5f4ff 50%, #fef7fb 100%);
    color: var(--color-foreground);
  }
  .dark body,
  html[data-theme="dark"] body {
    background: radial-gradient(1200px 600px at 0% -20%, rgba(143,135,241,0.4), transparent),
      radial-gradient(900px 700px at 90% 30%, rgba(233,165,241,0.25), transparent),
      linear-gradient(180deg, #12042a 0%, #1a0f38 40%, #0c081d 100%);
    color: #f8fbff;
  }
  .input-like {
    width: 100%;
    border-radius: var(--radius-xl);
    border-style: var(--tw-border-style);
    border-width: 1px;
    border-color: var(--color-slate-200);
    background-color: color-mix(in srgb, #fff 80%, transparent);
    @supports (color: color-mix(in lab, red, red)) {
      background-color: color-mix(in oklab, var(--color-white) 80%, transparent);
    }
    padding-inline: calc(var(--spacing) * 4);
    padding-block: calc(var(--spacing) * 2);
    color: var(--color-slate-900);
    &:is(.dark *) {
      border-color: var(--color-slate-700);
    }
    &:is(.dark *) {
      background-color: color-mix(in srgb, oklch(20.8% 0.042 265.755) 60%, transparent);
      @supports (color: color-mix(in lab, red, red)) {
        background-color: color-mix(in oklab, var(--color-slate-900) 60%, transparent);
      }
    }
    &:is(.dark *) {
      color: var(--color-white);
    }
  }
}
